<template>
    <form @submit.prevent="contactar" class="row">
        <div class="form-group col-12">
            Tu eMail
            <input type="email" class="form-control" v-model="datos.email" required placeholder="mi@email.com">
        </div>
        <div class="form-group col-12">
            Asunto
            <input type="text" class="form-control" v-model="datos.asunto" required placeholder="Mi asunto">
        </div>
        <div class="form-group col-12">
            Mensaje
            <textarea rows="5" class="form-control" v-model="datos.mensaje" required placeholder="Mi mensaje..."></textarea>
        </div>
        <div class="col-12">
            <button type="submit" class="btn btn-primary">Enviar</button>
        </div>
    </form>
</template>
<script>
export default {
    name: 'formularioContacto',
    props: ['base_url'],
    data(){
        return {
            datos: {
                email: '',
                mensaje: '',
                asunto: ''
            }
        }
    },
    methods: {
        contactar(){
            axios.post(this.base_url + '/api/contacto',this.datos).then((res)=>{
                toastr.success("Formulario de contacto enviado.");
                this.$emit("enviado");
            }).catch((e)=>{
                console.log(e);
                toastr.error("Error al enviar el formulario, intentelo mas tarde.");
            });
        }
    }
}
</script>

