  <div id="app">
    <Cabecera v-if="user.token" :items="menu2" @accion_loteria="showModal('loteriaModal')" @accion_contacto="showModal('contactoModal')" @accion_login="cerrar_sesion"/>
    <Cabecera v-else :items="menu" @accion_contacto="showModal('contactoModal')" @accion_login="showModal('loginModal')"/>
    
      <div>
