            >
          </b-carousel-slide>
          <b-carousel-slide 
            caption="Software"
            text="Experiencia en el desarrollo de Software"
          >
            <img slot="img"
                class="d-block img-fluid w-100"
                width="1024"
                height="480" 
                src="@/assets/carusel/software.jpg"
            >
          </b-carousel-slide>
        </b-carousel>
      </div>

    <hr>

    <div class="row">
      
      <div class="col-md-6 col-lg-4">
        <b-card title="Servidores" class="shadow m-1 border border-info rounded-pill">
          Experiencia en el uso de en el despliegue de servidores linux Apache, NodeJS
