        <div class="form-group col-12">
          Clave
          <input type="password" v-model="user.password" placeholder="Clave" class="form-control" required>
        </div>
        <div class="col-12">
          <button type="submit" class="btn btn-primary">Ingresar</button>
        </div>
      </form>
    </b-modal>

    <b-modal ref="loteriaModal" hide-footer title="Loteria">
      <div v-if="user.token">
        <form v-if="usuario.rol == 'Super Usuario' || usuario.permisos.indexOf('Agregar Loteria') != -1" class="row" @submit.prevent="agregarLoteria">
          <div class="form-group col-12">
            Nuevos Numeros (123456,123456,...)
            <input type="text" v-model="numeroN" required class="form-control">
          </div>
          <div class="col-12">
            <button type="submit" class="btn btn-primary">Guardar</button>
            <hr>
          </div>
        </form>
      </div>
      <form class="row" @submit.prevent="buscarLoteria">
        <div class="form-group col-12">
          Buscar Numeros (123456,123456,...)
          <input type="text" v-model="numeroB" required class="form-control">
        </div>
        <div class="col-12">
          <button type="submit" class="btn btn-primary">Buscar</button>
        </div>
      </form>
      <hr>
      <div class="container shadow my-1 py-1">
        <center>
          <strong>Numeros</strong>
        </center>
        <br>
        <ul v-if="numeros.length > 0">
          <li v-for="(num,index) in numeros" :key="index">
            <strong>
              {{num.numero}} - 
            </strong>
            {{ num.status }}
          </li>
        </ul>
      </div>
    </b-modal>
  </div>
</template>

<script>
import Cabecera from './components/Cabecera.vue'
import formularioContacto from './components/formularios/contacto.vue'

export default {
  name: 'app',
  components: {
    Cabecera,
    formularioContacto
  },
  data(){
    return{
      base_url: '',
      numeroB: '',
      numeroN: '',
      numeros: [],
      user: {
        user: '',
        password: '',
        token: ''
      },
      usuario: {},
      menu: [
        {
          url: '#',
          accion: 'login',
          name: 'Iniciar Sesion'
        },
        {
          url: '#',
          accion: 'contacto',
          name: 'Contacto'
        }
      ],
      menu2: [
        {
          url: '#',
          accion: 'login',
          name: 'Cerrar Sesion'
        },
        {
          url: '#',
          accion: 'contacto',
          name: 'Contacto'
        },
        {
          url: '#',
          accion: 'loteria',
          name: 'Loteria'
        }
      ]
    }
  },
  methods: {
    buscarLoteria(){
      let numArray = this.numeroB.split(',');
      
      axios.get(this.base_url + '/api/loteria').then((res) => {
        let numerox=res.data;
        this.numeros  = [];

        for (let i = 0; i < numerox.length; i++) {
            for (let j = 0; j < numArray.length; j++) {
              if (numerox[i].numero == numArray[j]) {
                this.numeros.push({
                  numero: numArray[j],
                  status: 'Ya jugo'
                });
                numArray.splice(j,1);
                j=numArray.length;
              } 
            }
        }
        for (let i = 0; i < numArray.length; i++) {
          this.numeros.push({
                  numero: numArray[i],
                  status: 'DISPONIBLE'
                });
        }
        this.numeroB = '';
      }).catch((e)=>{
        console.log(e);
        toastr.error("Error.");
      });
    },
    agregarLoteria(){
      let numArray = this.numeroN.split(',');
      for (let i = 0; i < numArray.length; i++) {
        let numN = {
          numero: numArray[i]
        };
        axios.post(this.base_url + '/api/loteria', numN,{headers: {
          user: this.usuario.user,
          token: this.usuario.token
        }}).then((res) => {
          if (res.data.msg_servidor) {
            toastr.error(res.data.msg_servidor);
          }else{
            toastr.success("Numero Agregado");
            this.numeroN = '';
          }
        }).catch((e) => {
          console.log(e);
          toastr.error("Error.");
        });    
      }
    },
    showModal(modal){
      this.$refs[modal].show();
    },
    hideModal(modal){
      this.$refs[modal].hide();
    },
    login(){
      axios.post(this.base_url + '/api/login', this.user).then((res)=>{
        if (res.data.token) {
          
          this.user.password = '';
          this.hideModal('loginModal');
          this.user.token = res.data.token;
          this.usuario = res.data;
          
        }else{
          toastr.error(res.data.msg_servidor);
        }
      }).catch((e)=>{
        console.log(e);
        toastr.error("Error al Iniciar Sesion");
      });
    },
    cerrar_sesion(){
      this.user = {
                    user: '',
                    password: '',
                    token: ''
                  };
      this.usuario = {};
    }
  }
}
</script>

